.background {
    position: relative;
    background-size: cover;
    height: 50vh;
}

.layer {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.paper-container {
    margin: -210px 30px 30px;
    padding: 30px;
    z-index: 3;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
    border-radius: 6px;    
}

@media (max-width: 830px) {
    .paper-container {
        margin-left: 10px;
        margin-right: 10px;
    }
}

.avatar {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
    align-items: center;
    margin-bottom: 25px;      
}

.avatar img {
    height: 120px;
    width: 120px;
    text-align: center;
    object-fit: cover;
    border-radius: 50%;
}

.grid-container {
    margin-top: 20px;
}

.grid-item {
    padding: 20px;
}

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

.align-center {
    text-align: center;
}

.not-underline {
    text-decoration: none;    
}

.white {
    color: white;
}

.primary {
    color: #2196f3;
}

.title-1, 
.title-2,
.title-3,
.title-4,
.title-5,
.title-6 {
    color: rgba(0, 0, 0, 0.87);
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.title-1 {
    font-size: 6rem;
    font-weight: 300;
    line-height: 1;
    letter-spacing: -0.01562em;
}

.title-2 {
    font-size: 3.75rem;
    font-weight: 300;
    line-height: 1;
    letter-spacing: -0.00833em;
}

.title-3 {    
    font-size: 3rem;
    font-weight: 400;
    line-height: 1.04;
    letter-spacing: 0em;
}

.title-4 {    
    font-size: 2.125rem;
    font-weight: 400;
    line-height: 1.17;
    letter-spacing: 0.00735em;
}
